@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fraunces:wght@500&display=swap);
a, article, body, button, div, fieldset, footer, input, form, h1, h2, header, html, i, img, label, li, main, nav, p, section, small, span, strong, textarea, time, ul {
  background: transparent;
  border: 0;
  box-sizing: inherit;
  color: inherit;
  font: inherit;
  margin: 0;
  outline: 0;
  padding: 0;
  text-align: inherit;
  text-decoration: inherit;
  vertical-align: inherit;
  font-family: 'Open Sans', sans-serif;
}

ul {
  list-style: none;
}

img {
  display: block;
  height: auto;
  width: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(0,0,0, 0.05);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.login-form {
	margin: 5px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	width: 275px;
	border: 1px solid black;
	height: 250px;
	background-color: #fff9f3;
}

.signup-form {
	margin: 5px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	width: 400px;
	height: 650px;
	max-height: 800px;
	border: 1px solid black;
	background-color: #fff9f3;
}

.review-form {
	margin: 5px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	width: 400px;
	height: 250px;
	max-height: 800px;
	border: 1px solid black;
	background-color: #fff9f3;
}

.product-form {
	margin: 5px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	width: 450px;
	height: 850px;
	max-height: 800px;
	border: 1px solid black;
	background-color: #fff9f3;
}

.deletion-form {
	margin: 10px;
	width: 400px;
	height: 320px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	border: 1px solid black;
	background-color: #fff9f3;
}

.form-label {
	position: relative;
	top: 2px;
	white-space: nowrap;
	left: 10px;
	font-size: 9pt;
	text-align: left;
	background-color: rgba(0, 0, 0, 0);
	width: 60px;
}

.form-textarea {
	width: 380px;
	height: 100px;
	resize: none;
	background-color: white;
	border: 2px solid #4af99b;
}

.form-input {
	background-color: white;
	padding: 3px;
	border-radius: 5px;
	border: 2px solid #4af99b;
}

.form-input:focus {
	background-color: #d2fde6;
}

.signup-form-input {
	background-color: white;
	width: 380px;
	padding: 3px;
	border-radius: 5px;
	border: 2px solid #4af99b;
}

.signup-form-input:focus {
	background-color: #d2fde6;
}

.field-holder {
	display: flex;
	flex-direction: column;
}

.login-button {
	background-color: #8efbc1;
	width: 120px;
	height: 30px;
	font-size: 11pt;
	text-align: center;
	border-radius: 10px;
	border: 1px solid black;
	cursor: pointer;
	transition: all 0.2s ease-in-out;
}

.login-button:hover {
	background-color: #4af99b;
	transform: translateY(-1px);
}

.login-spacer {
	display: inline-block;
	width: 10px;
}

.password-warning {
	text-align: center;
	font-size: 9pt;
	color: red;
}

.upload-choice {
	display: flex;
	border: 1px solid black;
	border-radius: 10px;
	width: 120px;
	height: 30px;
	background-color: #8efbc1;
	text-align: center;
	align-items: center;
	justify-content: center;
	transition: all 0.2s ease-in-out;
}

.upload-choice:hover {
	background-color: #4af99b;
	transform: translateY(-1px);
}

input[type="file"] {
	position: absolute;
	z-index: -1;
	top: 56px;
	left: 10px;
	font-size: 15px;
	color: rgba(153, 153, 153, 0);
}

.form-upload-div {
	width: 400px;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.form-image-holder {
	height: 200px;
}

.form-image-preview {
	height: 100%;
	width: auto;
}

.user-edit-instruction {
	font-size: 10pt;
}

.warning-header {
	color: red;
	text-align: center;
}

.warning-text {
	color: red;
	font-size: 12px;
	padding-bottom: 20px;
	padding-top: 10px;
	text-align: center;
}

.warning-div {
	background-color: #ffdfdf;
	width: 400px;
	padding-top: 10px;
	border-bottom: 1px solid black;
}

.warning-input {
	background-color: white;
	width: 370px;
	padding: 3px;
	margin-left: 10px;
	border-radius: 5px;
	border: 2px solid #fe2020;
}

.warning-input:focus {
	background-color: #ffdfdf;
}

.warning-button {
	background-color: #fe8080;
	width: 120px;
	height: 30px;
	font-size: 11pt;
	margin-bottom: 15px;
	text-align: center;
	border-radius: 10px;
	border: 1px solid black;
	cursor: pointer;
	transition: all 0.2s ease-in-out;
}

.warning-button:hover {
	background-color: #fe2020;
	transform: translateY(-1px);
}

.product-form-select {
	background-color: white;
	width: 390px;
	height: 33px;
	border-radius: 5px;
	border: 2px solid #4af99b;
}

.product-form-select:focus {
	background-color: #d2fde6;
}

.select-div {
	width: 390px;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}

.product-select-input {
	width: 200px;
	background-color: white;
	padding: 3px;
	border-radius: 5px;
	border: 2px solid #4af99b;
}

.product-select-input:focus {
	background-color: #d2fde6;
}

.product-unit-select {
	background-color: white;
	width: 120px;
	height: 33px;
	border-radius: 5px;
	border: 2px solid #4af99b;
}

.product-unit-select:focus {
	background-color: #d2fde6;
}

/* .sold-out {
	margin-bottom: 15px;
} */

.cart-item {
    margin: 10px;
    width: 1000px;
    margin: 20px auto;
    border: 1px solid black;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.cart-image-container {
	height: 150px;
}

.cart-image {
	height: 100%;
	width: auto;
}

.checkout-container {
    display: flex;
    flex-direction: row;
    width: 800px;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}

.cart-empty-text {
    text-align: center;
    padding: 40px;
    font-size: 18pt;
}

.empty-cart-div {
    width: 800px;
    margin: 100px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cart-remove-button {
    font-size: 18pt;
}

.product-container {
    width: 400px;
    height: 310px;
    margin: 30px;
    border: 1px solid black;
    border-radius: 3px;
}

.product-preview-image-container {
    width: 400px;
    height: 250px;
    overflow: hidden;
    display: flex;
    /* justify-content: center; */
    align-items: center;
}

.product-preview-image {
    overflow: hidden;
    width: 400px;
    padding-bottom: 9px;
    padding-top: 20px;
}

.product-preview-header {
    display: flex;
    padding: 3px;
    height: 50px;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background: linear-gradient(#ffe1bf, #fff0df);
    border-bottom: 1px solid black;
}

.product-preview-name {
    font-weight: 600;
    font-size: 16pt;
    max-width: 250px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.product-preview-rating {
    width: 380px;
    display: flex;
    justify-content: space-between;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    position: relative;
    bottom: 18px;
    padding-left: 14px;
    padding-right: 6px;
}

.product-preview-star {
    padding-top: 2px;
    padding-left: 5px;
    font-size: 12px;
}

.preview-rating {
    text-align: right;
    align-items: center;
    display: flex;
    align-items: center;
}

.forms-holder {
    background-color: red;
    width: 800px;
    display: flex;
    justify-content: space-between;
    margin: 0px auto;
}

.login-splash-div {
    text-align: center;
}

.signup-splash-div {
    text-align: center;
}

.splash-background {
    width: 100vw;
    height: 675px;
}

.splash-icons {
    margin-top: 80px;
    width: 800px;
    display: flex;
    justify-content: space-around;
    font-size: 80px;
}

.splash-title {
    font-size: 36pt;
    margin-bottom: 50px;
    margin-top: 75px;
}

.splash-blurbs {
    font-size: 24pt;
    text-align: center;
}

.splash-about {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 1000px;
    margin: 0 auto;
}

.blurb {
    padding: 20px;
}

.modal-blur {
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
}

.modal-content {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.modal-button-holder {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    color: white;
}

.profile-button-dropdown {
	position: absolute;
	background-color: #FFF9F3;
	top: 80px;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 150px;
	height: 170px;
	border: 1px solid black;
}

.nav-user-icon {
	width: 50px;
	height: 50px;
}

.nav-user-image {
	max-width: 100%;
	height: auto;
	border-radius: 50%;
	border: 1px solid black;
	background-color: rgba(0, 0, 0, 0.8);
}

.nav-user-image:hover {
	cursor: pointer;
}

.nav-holder {
	padding-top: 5px;
}

.profile-dropdown-links {
	padding-top: 10px;
	padding-bottom: 10px;
	text-align: center;
	width: 150px;
	border-top: 1px solid black;
}

.profile-dropdown-links:hover {
	cursor: pointer;
}

.searchbar-container {
	width: 800px;
	height: 35px;
	border: 2px solid black;
	border-radius: 50px;
	display: flex;
	align-items: center;
}

.searchbar-form {
	width: 750px;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.searchbar-input {
	width: 730px;
	padding-left: 22px;
	height: 35px;
	border-right: 3px solid black;
}

.searchbar-submit-button {
	height: 38px;
	padding-left: 12px;
	padding-right: 18px;
	border-radius: 0px 50px 50px 0px;
	border: none;
	background-color: rgba(0, 0, 0, 0);
	cursor: pointer;
	transition: all .2s ease-in-out;
}

.searchbar-submit-button:hover {
	background-color: black;
	color: white;
}

.navbar-main-div {
	display: flex;
	justify-content: center;
	position: static;
	background: linear-gradient(#ffe1bf, #fff0df);
	height: 80px;
	border-bottom: 2px solid black;
}

.navbar-widgets {
	width: 1600px;
	display: flex;
	position: relative;
	justify-content: space-around;
	align-items: center;
}

.nav-control-buttons {
	display: flex;
	justify-content: space-around;
	width: 150px;
	align-items: center;
}

.cart-icon {
	font-size: 30px;
	position: relative;
}

.cart-icon:hover {
	cursor: pointer;
}

.cart-items {
	position: absolute;
	top: 5px;
	right: 10px;
	color: white;
	font-size: 9pt;
}

.navbar-logo {
	font-family: "Fraunces", serif;
	font-size: 32px;
	font-weight: 600;
}

.navbar-category-container {
	background-color: #4AF99B;
	height: 30px;
	width: 100%;
	border-bottom: 2px solid black;
}

.navbar-categories {
	margin: 0 auto;
	padding-top: 2px;
	width: 800px;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.feed-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 1400px;
    margin: 0px auto;
}

.feed-header {
    padding-top: 20px;
    font-size: 18pt;
    text-align: center;
}

.product-view-controls {
	width: 400px;
	height: 100px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	grid-area: controls;
	margin: 0 auto auto auto;
}

.seller-controls>button{
	margin-bottom: 15px;
}

.product-view-details {
	width: 350px;
	height: 400px;
	border: 1px solid black;
	border-radius: 3px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	padding: 10px;
	font-size: 11pt;
	grid-area: details;
	margin: 100px auto 0 auto;
	background-color: #fff9f3;
}

.product-view-image-container {
	height: 500px;
	border: 1px solid black;
	border-radius: 3px;
	grid-area: photo;
	margin: 100px auto 0 auto;
	background-color: #fff9f3;
}

.product-view-image {
	height: 100%;
	width: auto;
}

.product-view-name {
	font-size: 18pt;
	text-decoration: underline;
}

.product-detail-header {
	font-weight: 700;
}

.product-view-ratings {
	width: 350px;
	display: flex;
	flex-direction: column;
	align-items: center;
	border: 1px solid black;
	border-radius: 3px;
	grid-area: ratings;
	margin: 0 auto 100px auto;
	background-color: #fff9f3;
}

.product-review {
	width: 250px;
	padding: 10px;
}

.review-edit-button {
	width: 20px;
	height: 20px;
	cursor: pointer;
}

.review-container {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.product-view-grid {
	width: 1500px;
	margin: 0 auto;
	display: grid;
	grid-template-columns: 1fr 2fr;
	grid-template-rows: 605px 100px 1fr;
	grid-template-areas:
		"details photo"
		"ratings controls"
		"ratings controls";
}

.product-view-ratings-header {
	font-size: 18pt;
	padding: 10px;
	text-decoration: underline;
}

.review-user {
	font-size: 10pt;
}

.rating-thank-you {
	font-size: 12px;
	width: 300px;
	text-align: center;
	padding-bottom: 10px;
	margin-bottom: 10px;
	border-bottom: 1px solid black;
}

.no-reviews {
	padding-bottom: 20px;
	font-weight: 700;
}

.product-view-review-button {
	padding-bottom: 10px;
	border-bottom: 1px solid black;
	width: 300px;
	margin-bottom: 10px;
	display: flex;
	justify-content: center;
}

.fa-pencil-alt{
	cursor: pointer;
}

.profile-banner-container {
	max-height: 250px;
    overflow: hidden;
	border-bottom: 1px solid black;
}

.profile-banner {
    height: auto;
    width: 100%;
}

.profile-avatar-container {
    margin: 25px;
    margin-top: 75px;
	height: 200px;
	width: 250px;
	display: flex;
	justify-content: center;
}

.profile-username-container {
	width: 300px;
	margin-left: 25px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.profile-info-container {
	margin: 25px;
	width: 400px;
}

.profile-products-container {
	max-width: 1400px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.profile-products-header {
	margin-top: 25px;
	font-size: 14pt;
	text-align: center;
	text-decoration: underline;
	-webkit-text-decoration-color: #ffe1bf;
	        text-decoration-color: #ffe1bf;
	text-decoration-thickness: 3px;
    font-weight: 600;
}

.profile-avatar {
	height: 100%;
	width: auto;
	border-radius: 50%;
	background-color: rgba(0, 0, 0, 0.5);
}

.profile-username {
	font-size: 18pt;
	font-weight: 600;
}

.profile-info-header {
	text-decoration: underline;
	-webkit-text-decoration-color: #ffe1bf;
	        text-decoration-color: #ffe1bf;
	text-decoration-thickness: 3px;
	margin-top: 50px;
	margin-bottom: 25px;
	font-size: 14pt;
    font-weight: 600;
}

.profile-bio {
	margin-top: 25px;
}

.profile-grid-div {
	display: grid;
	grid-template-columns: 1fr, 3fr;
	grid-template-areas: "info products";
}

.profile-user-grid-div {
	grid-area: info;
}

.profile-products-grid-div {
	grid-area: products;
}

.profile-following-link-container {
	margin: 15px 0;
}

.profile-following-link {
	width: 250px;
	display: flex;
	justify-content: space-between;
}

.profile-following-link-icon {
	padding-right: 30px;
}

.profile-username-container > button {
	margin-bottom: 0px;

}

.footer-div {
    margin-top: 50px;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid black;
}

.footer-name {
    padding: 50px;
}

.footer-links {
    width: 300px;
    font-size: 30px;
    display: flex;
    justify-content: space-around;
    padding: 50px;
}

